
.dropbox[data-v-b9f65170] {
  outline: 2px dashed grey; /* the dash box */
  outline-offset: -10px;
  background: lightcyan;
  color: dimgray;
  padding: 10px 10px;
  min-height: 200px; /* minimum height */
  position: relative;
  cursor: pointer;
}
.input-file[data-v-b9f65170] {
  opacity: 0; /* invisible but it's there! */
  width: 100%;
  height: 200px;
  position: absolute;
  cursor: pointer;
}
.dropbox[data-v-b9f65170]:hover {
  background: lightblue; /* when mouse over to the drop zone, change color */
}
.dropbox p[data-v-b9f65170] {
  font-size: 1.2em;
  text-align: center;
  padding: 50px 0;
}



